import React, { Dispatch, SetStateAction } from "react";
import styled from "styled-components";
import RightPaperImage from "../../common/images/RightPaper.png";
import LeftPaperImage from "../../common/images/LeftPaper.png";
import LeftBracket from "../../common/images/BracketsLeft.png";
import RightBracket from "../../common/images/BracketsRight.png";
import Avatar from "../../common/images/Photo.png";
import Banner from "../../common/images/Banner.png";
import Telegram from "../../common/images/Telegram.png";
import Gmail from "../../common/images/Logo/Gmail.png";
import Git from "../../common/images/Logo/git.png";
import StackIcon from "../../common/images/StackIcon.jpg";
import Portal from "./Portal1.gif";
import HtmlLogo from "../../common/images/Logo/html.png";
import CssLogo from "../../common/images/Logo/css.png";
import Typescript from "../../common/images/Logo/TS.png";
import ReactLogo from "../../common/images/Logo/react.png";
import ReduxLogo from "../../common/images/Logo/redux.png";
import Mongo from "../../common/images/Logo/MongoDB.png";
import Express from "../../common/images/Logo/express.png";
import Socket from "../../common/images/Logo/socket-io.png";
const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  top: 30px;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Item = styled.div`
  width: 1110px;
  height: 690px;
  display: flex;
`;
const LeftPaper = styled.div`
  width: 555px;
  height: 690px;

  background: url(${LeftPaperImage});
  box-sizing: border-box;
  padding: 30px 50px;
`;
const RightPaper = styled.div`
  width: 555px;
  background: url(${RightPaperImage});
  height: 690px;

  box-sizing: border-box;
  padding: 50px;
`;
const Chapter = styled.div`
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  img {
    width: 100px;
    height: 4px;
  }
`;
const ChapterText = styled.div`
  font-family: Pixel;
  font-size: 25px;
`;
const Nickname = styled.div`
  font-family: Pixel2;
  font-size: 25px;
  text-align: center;
  margin: 25px 0px 15px 0px;
`;

const AvatarContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const BannerItem = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 10px;
`;

const Social = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-left: 55px;
  margin-top: 30px;
`;

const SocialItem = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 15px;
  font-family: Pixel;
`;

const Title = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  font-family: Rubik Vinyl;
  font-size: 45px;
`;

const Description = styled.div`
  font-family: Pixel;
  font-size: 17px;
  margin: 10px 0px 10px 0px;
  text-align: center;
`;

const StackContainer = styled.div`
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  margin: 20px;
  justify-content: center;
`;

const StackItem = styled.div`
  width: 50px;
  height: 50px;
  background: url(${StackIcon});
  background-size: contain;
  display: flex;
  align-items: center;
  justify-content: center;
  img{
    width:30px;
    height:30px;
  }
`;
const PortalContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  img {
    width: 120px;
    height: 140px;
    cursor: pointer;
  }
`;

type Props = {
  setPortfolioModal: Dispatch<SetStateAction<boolean>>;
};

function Portfolio({ setPortfolioModal }: Props) {
  return (
    <Wrapper onClick={() => setPortfolioModal(false)}>
      <Item>
        <RightPaper>
          <Chapter>
            <img src={LeftBracket} />
            <ChapterText>AUTHOR</ChapterText>
            <img src={RightBracket} />
          </Chapter>
          <Nickname>Sabaka137</Nickname>
          <AvatarContainer>
            <img src={Avatar} />
          </AvatarContainer>
          <BannerItem>
            <img src={Banner} />
          </BannerItem>
          <Social>
            <SocialItem>
              <img src={Telegram} />
              <div>https://t.me/sabaka137</div>
            </SocialItem>
            <SocialItem>
              <img src={Gmail} />
              <div>staspopov.work@gmail.com</div>
            </SocialItem>
            <SocialItem>
              <img src={Git} />
              <div>https://github.com/sabaka137</div>
            </SocialItem>
          </Social>
        </RightPaper>
        <LeftPaper>
          <Title> </Title>
          <Description>
                   ,    .   
                   ,   
                   .  
                .
          </Description>
          <Chapter>
            <img src={LeftBracket} />
            <ChapterText></ChapterText>
            <img src={RightBracket} />
          </Chapter>
          <StackContainer>
            <StackItem>
              <img src={HtmlLogo} />
            </StackItem>
            <StackItem><img src={CssLogo} /></StackItem>
            <StackItem><img src={Typescript} /></StackItem>
            <StackItem><img src={Typescript} /></StackItem>
            <StackItem><img src={ReactLogo} /></StackItem>
            <StackItem>
              <img src={ReduxLogo} />
            </StackItem>
            <StackItem> <img src={Mongo} /></StackItem>
            <StackItem><img src={Express} /></StackItem>
            <StackItem><img src={Socket} /></StackItem>
          </StackContainer>
          <Chapter>
            <img src={LeftBracket} />
            <ChapterText></ChapterText>
            <img src={RightBracket} />
          </Chapter>
          <PortalContainer>
            <img src={Portal} />
          </PortalContainer>
        </LeftPaper>
      </Item>
    </Wrapper>
  );
}

export default Portfolio;
