import React, { Dispatch, SetStateAction,useState,useEffect } from 'react'
import BG from '../../common/images/DialogBg.jpg'
import styled from 'styled-components'

const Wrapper = styled.div`
width:100%;
height:100%;
top:0;
left:0;
position: absolute;
display:flex;
align-items:flex-end;
`
const Item = styled.div`
width:1100px;
height:280px;
background:url(${BG});
background-size: contain;
background-repeat: no-repeat;
background-position: center; 
margin-left:440px;
display:flex;
gap:12px;
flex-direction:column;
box-sizing:border-box;
padding:40px 35px;
`
const Text = styled.div`
color:white;
font-size:18px;
font-family:Pixel;
overflow-y:auto;

`
const Header = styled.div`
color:white;
font-weigh:700;
font-size:36px;
font-family:Pixel;
`

type Props = {
    setDialogModal:Dispatch<SetStateAction<null | {speakerName:string,text:string}>>
    dialog:{
        speakerName: string;
        text: string;
    }
}

function DialogueModal({setDialogModal,dialog}: Props) {
  const [currentText, setCurrentText] = useState('');
  const [currentIndex, setCurrentIndex] = useState(0);
  const delay = 15
  useEffect(() => {
    if (currentIndex < dialog.text.length) {
      const timeout = setTimeout(() => {
        setCurrentText(prevText => prevText + dialog.text[currentIndex]);
        setCurrentIndex(prevIndex => prevIndex + 1);
      }, delay);
  
      return () => clearTimeout(timeout);
    }
  }, [currentIndex, delay, dialog.text]);
  return (
<Wrapper onClick={(e)=>(e.preventDefault(),setDialogModal(null))}>
    <Item onClick={(e)=>(e.stopPropagation(),setCurrentIndex(dialog.text.length),setCurrentText(dialog.text))}>
        <Header>{dialog.speakerName}</Header>
        <Text>{currentText}</Text>
    </Item>
</Wrapper>
  )
}

export default DialogueModal