import React, { Dispatch, SetStateAction } from "react";
import styled from "styled-components";
import Paper from "../../common/images/Test1.png";
import Luffy from "./Luffy.gif";
const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Item = styled.div`
  width: 1190px;
  height: 740px;
  background: url(${Paper});
  display: flex;
  img {
    width: 250px;
    height: 250px;
    margin-left: 65px;
    margin-top: 70px;
  }
`;
type Props = {
  setAboutModal: Dispatch<SetStateAction<boolean>>;
};

function AboutMeModal({ setAboutModal }: Props) {
  return (
    <Wrapper onClick={()=>setAboutModal(false)}>
      <Item>
        <img src={Luffy} />
      </Item>
    </Wrapper>
  );
}

export default AboutMeModal;
