import React, { Dispatch, SetStateAction } from "react";
import styled from "styled-components";
import Paper from '../../common/images/PPTEST6.png'
const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  left:0;
  right:0;
  top:0;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Item = styled.div`
  width: 559px;
  height: 693px;
  background: url(${Paper});
  display: flex;
  box-sizing: border-box;
  font-family:Pixel;
  font-size:22px;
  padding: 50px;
`;
type Props = {
    setGreatingsModal:Dispatch<SetStateAction<boolean>>
};

function GreatingModal({setGreatingsModal}: Props) {
  return (
    <Wrapper onClick={()=>setGreatingsModal(false)}>
     <Item></Item>
    </Wrapper>
  );
}

export default GreatingModal;
