import React, { useState, useEffect, Dispatch, SetStateAction } from "react";
import { Sprite, useTick } from "@pixi/react";
import { ACTION_HITBOX, ISLAND_INFO, MAP_SETTING, TELEPORT_POINT } from "../../constant/data";
import * as PIXI from "pixi.js";
import { IModifiedAnimatedSprite, IModifiedSprite } from "../../types/pixi-react";
import { Teleport } from "../../utils/TeleportUser";

type Props = {
  collisionsRefs: React.MutableRefObject<IModifiedSprite[]>;
  characterRef: React.RefObject<PIXI.AnimatedSprite>;
  foregroundRef: React.RefObject<PIXI.Sprite>;
  NPCRef: React.MutableRefObject<IModifiedAnimatedSprite[]>;
  actionsRef: React.MutableRefObject<PIXI.Sprite[]>;
  entryRef: React.MutableRefObject<PIXI.Sprite[]>;
  setDialogModal: Dispatch<SetStateAction<null | { speakerName: string; text: string }>>;
  navigationRef: React.MutableRefObject<HTMLDivElement[]>;
  setPortfolioModal: Dispatch<SetStateAction<boolean>>;
  setTextModal: Dispatch<SetStateAction<string | null>>;
};

function Background({
  collisionsRefs,
  characterRef,
  foregroundRef,
  NPCRef,
  actionsRef,
  setDialogModal,
  setPortfolioModal,
  navigationRef,
  setTextModal,
  entryRef,
}: Props) {
  const [position, setPosition] = useState({
    x: -MAP_SETTING.general.spawnPoint.x * MAP_SETTING.general.scale + window.innerWidth / 2,
    y: -(MAP_SETTING.general.spawnPoint.y * MAP_SETTING.general.scale) + window.innerHeight / 2,
  });
  const [isPressed, setIsPressed] = useState({
    w: false,
    s: false,
    d: false,
    a: false,
  });
  const [lastKey, setLastKey] = useState("");

  function keyDown(code: string) {
    let temp = {
      w: code === "KeyW" ? true : false,
      s: code === "KeyS" ? true : false,
      d: code === "KeyD" ? true : false,
      a: code === "KeyA" ? true : false,
    };
    setIsPressed(temp);
    setLastKey(code);
  }
  function keyUp(code: string) {
    let temp = {
      w: code === "KeyW" ? false : true,
      s: code === "KeyS" ? false : true,
      d: code === "KeyD" ? false : true,
      a: code === "KeyA" ? false : true,
    };
    setIsPressed(temp);
  }
  useEffect(() => {
    window.addEventListener("keydown", (e) => keyDown(e.code));
    window.addEventListener("keyup", (e) => {
      keyUp(e.code);
    });
    navigationRef.current.forEach((item) => {
      item.addEventListener("click", () => {
        if (item.id === "history") {
          Teleport({
            collisionsRefs,
            foregroundRef,
            NPCRef,
            actionsRef,
            setPosition,
            teleportPointPosition: TELEPORT_POINT.history,
            entryRef
          });
          setDialogModal({
            speakerName:ISLAND_INFO[0].content.speakerName,
            text:ISLAND_INFO[0].content.text
          })
        }
        if (item.id === "portfolio") {
          Teleport({
            collisionsRefs,
            foregroundRef,
            NPCRef,
            actionsRef,
            setPosition,
            teleportPointPosition: TELEPORT_POINT.portfolio,
            entryRef
          });
          setDialogModal({
            speakerName:ISLAND_INFO[1].content.speakerName,
            text:ISLAND_INFO[1].content.text
          })
        }
        if (item.id === "cemetery") {
          Teleport({
            collisionsRefs,
            foregroundRef,
            NPCRef,
            actionsRef,
            setPosition,
            teleportPointPosition: TELEPORT_POINT.cemetry,
            entryRef
          });
          setDialogModal({
            speakerName:ISLAND_INFO[2].content.speakerName,
            text:ISLAND_INFO[2].content.text
          })
        }
        if (item.id === "main") {
          Teleport({
            collisionsRefs,
            foregroundRef,
            NPCRef,
            actionsRef,
            setPosition,
            teleportPointPosition: TELEPORT_POINT.main,
            entryRef
          });
        }
      });
    });
    window.addEventListener("keypress", (e) => {
      if (e.code == "Space") {
        actionsRef.current.forEach((action) => {
          if (action.roundPixels) {
            ACTION_HITBOX.forEach((item) => {
              if (item.id === action.renderId) {
                if (item.type === "dialogue") {
                  setDialogModal({
                    speakerName: item.content.speakerName,
                    text: item.content.text,
                  });
                }
                if (item.type === "textWindow") {
                  setTextModal(item.content.text);
                }
                if (item.type === "link") {
                  window.open("123", "_bland")!.focus();
                }
                if (item.type === "portfolio") {
                  setPortfolioModal(true);
                }
              }
            });
          }
        });
      }
    });
  }, []);
  function rectangularCollision({ rectangle1, rectangle2 }: any) {
    return (
      rectangle1.x + rectangle1.width / 2 >= rectangle2.x &&
      rectangle1.y <= rectangle2.y + rectangle2.height &&
      rectangle1.x <= rectangle2.x + rectangle2.width &&
      rectangle1.y + rectangle1.height / 2 >= rectangle2.y
    );
  }

  useTick((delta) => {
    let moving = true;
    if (isPressed.w && lastKey == "KeyW") {
      collisionsRefs.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x,
              y: el.y + delta * 5,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          moving = false;
        }
      });
      actionsRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x,
              y: el.y + delta * 5,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          el.roundPixels = true;
        } else {
          el.roundPixels = false;
        }
      });
      entryRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x,
              y: el.y + delta * 5,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          if (!el.roundPixels) {
            el.roundPixels = true;
            ISLAND_INFO.forEach((item) => {
              if (item.id === el.renderId) {
                setDialogModal({ speakerName: item.content.speakerName, text: item.content.text });
              }
            });
          }
        } else {
          //
        }
      });

      if (moving) {
        collisionsRefs.current.forEach((el) => (el.y += delta * 5));
        NPCRef.current.forEach((el) => (el.y += delta * 5));
        actionsRef.current.forEach((el) => (el.y += delta * 5));
        entryRef.current.forEach((el) => (el.y += delta * 5));
        if (foregroundRef.current !== null) {
          foregroundRef.current.y += delta * 5;
        }
        setPosition({ x: position.x, y: position.y + delta * 5 });
      }
    }
    if (isPressed.s && lastKey == "KeyS") {
      collisionsRefs.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x,
              y: el.y - delta * 5,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          moving = false;
        }
      });
      actionsRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x,
              y: el.y - delta * 5,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          el.roundPixels = true;
        } else {
          el.roundPixels = false;
        }
      });
      entryRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x,
              y: el.y - delta * 5,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          //  
          if (!el.roundPixels) {
            el.roundPixels = true;
            ISLAND_INFO.forEach((item) => {
              if (item.id === el.renderId) {
                setDialogModal({ speakerName: item.content.speakerName, text: item.content.text });
              }
            });
          }
        } else {
          //
        }
      });
      if (moving) {
        collisionsRefs.current.forEach((el) => (el.y -= delta * 5));
        NPCRef.current.forEach((el) => (el.y -= delta * 5));
        actionsRef.current.forEach((el) => (el.y -= delta * 5));
        entryRef.current.forEach((el) => (el.y -= delta * 5));
        if (foregroundRef.current !== null) {
          foregroundRef.current.y -= delta * 5;
        }
        setPosition({ x: position.x, y: position.y - delta * 5 });
      }
    }
    if (isPressed.d && lastKey == "KeyD") {
      collisionsRefs.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x - delta * 5,
              y: el.y,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          moving = false;
        }
      });
      actionsRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x - delta * 5,
              y: el.y,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          el.roundPixels = true;
        } else {
          el.roundPixels = false;
        }
      });
      entryRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x - delta * 5,
              y: el.y,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          //  
          if (!el.roundPixels) {
            el.roundPixels = true;
            ISLAND_INFO.forEach((item) => {
              if (item.id === el.renderId) {
                setDialogModal({ speakerName: item.content.speakerName, text: item.content.text });
              }
            });
          }
        } else {
          //
        }
      });
      if (moving) {
        collisionsRefs.current.forEach((el) => (el.x -= delta * 5));
        NPCRef.current.forEach((el) => (el.x -= delta * 5));
        actionsRef.current.forEach((el) => (el.x -= delta * 5));
        entryRef.current.forEach((el) => (el.x -= delta * 5));
        if (foregroundRef.current !== null) {
          foregroundRef.current.x -= delta * 5;
        }
        setPosition({ x: position.x - delta * 5, y: position.y });
      }
    }
    if (isPressed.a && lastKey == "KeyA") {
      collisionsRefs.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x + delta * 5,
              y: el.y,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          moving = false;
        }
      });
      actionsRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x + delta * 5,
              y: el.y,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          el.roundPixels = true;
        } else {
          el.roundPixels = false;
        }
      });
      entryRef.current.forEach((el) => {
        if (
          rectangularCollision({
            rectangle1: characterRef.current,
            rectangle2: {
              x: el.x + delta * 5,
              y: el.y,
              width: el.width,
              height: el.height,
            },
          })
        ) {
          //  
          if (!el.roundPixels) {
            el.roundPixels = true;
            ISLAND_INFO.forEach((item) => {
              if (item.id === el.renderId) {
                setDialogModal({ speakerName: item.content.speakerName, text: item.content.text });
              }
            });
          }
        } else {
          //
        }
      });
      if (moving) {
        collisionsRefs.current.forEach((el) => (el.x += delta * 5));
        NPCRef.current.forEach((el) => (el.x += delta * 5));
        actionsRef.current.forEach((el) => (el.x += delta * 5));
        entryRef.current.forEach((el) => (el.x += delta * 5));
        if (foregroundRef.current !== null) {
          foregroundRef.current.x += delta * 5;
        }
        setPosition({ x: position.x + delta * 5, y: position.y });
      }
    }
  });
  return (
    <>
      <Sprite image={MAP_SETTING.background} x={position.x} y={position.y} />
    </>
  );
}

export default Background;
