import React, { useRef, useState, useEffect } from "react";
import "./App.css";
import Movables from "./components/Movables/Movables";
import { Stage } from "@pixi/react";
import GreatingModal from "./components/Modal/GreatingModal";
import DialogueModal from "./components/Modal/DialogueModal";
import AboutMeModal from "./components/Modal/AboutMeModal";
import NavBar from "./components/Modal/NavBar";
import Portfolio from "./components/Modal/Portfolio";
import TextModal from "./components/Modal/TextModal";
import * as PIXI from "pixi.js";
import Test1 from "./common/images/Test1.png";
import GIf from './components/Modal/Luffy.gif'
import useImagePreloader from "./components/useImagePreloader";
import Dialog from './common/images/DialogBg.jpg'
function App() {
  const { imagesPreloaded } = useImagePreloader([Test1,GIf,Dialog])
  const [greatingsModal, setGreatingsModal] = useState(true);
  const [dialogModal, setDialogModal] = useState<{ speakerName: string; text: string } | null>(
    null
  );
  const [aboutModal, setAboutModal] = useState(false);
  const [portfolioModal, setPortfolioModal] = useState(false);
  const [textModal, setTextModal] = useState<string | null>(null);
  const navigationRef = useRef<HTMLDivElement[]>([]);
  const addNavToRef = (el: HTMLDivElement) => {
    if (el && !navigationRef.current.includes(el)) {
      navigationRef.current.push(el);
    }
  };


  return (
    <>
      <Stage width={window.innerWidth} height={window.innerHeight} options={{ autoDensity: true }}>
        <Movables
          navigationRef={navigationRef}
          setPortfolioModal={setPortfolioModal}
          setDialogModal={setDialogModal}
          setTextModal={setTextModal}
        />
      </Stage>
      <NavBar addNavToRef={addNavToRef} setAboutModal={setAboutModal} />
      {textModal !== null && <TextModal text={textModal} setTextModal={setTextModal} />}
      {aboutModal && <AboutMeModal setAboutModal={setAboutModal} />}
      {portfolioModal && <Portfolio setPortfolioModal={setPortfolioModal} />}
      {greatingsModal && <GreatingModal setGreatingsModal={setGreatingsModal} />}
      {dialogModal !== null && (
        <DialogueModal dialog={dialogModal} setDialogModal={setDialogModal} />
      )}
    </>
  );
}

export default App;
