import React from 'react'
import { MAP_SETTING } from '../../constant/data'
import { Sprite } from '@pixi/react'
import * as PIXI from "pixi.js";
type Props = {
  foregroundRef: React.RefObject<PIXI.Sprite>
}

function Foreground({foregroundRef}: Props) {
  return (
    <Sprite
    image={MAP_SETTING.foreground}
    ref={foregroundRef}
    x={(-MAP_SETTING.general.spawnPoint.x * 4.5 + window.innerWidth / 2)}
    y={(-MAP_SETTING.general.spawnPoint.y * 4.5 + window.innerHeight / 2)}
  />
  )
}

export default Foreground