import React, { Dispatch, SetStateAction } from "react";
import styled from "styled-components";
const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  top: 10px;
  left: 0;
  display: flex;
  justify-content: center;
`;
const Item = styled.div`
  width: 890px;
  height: 60px;
  background: #3b201a;
  border: 3px solid white;
  border-radius: 50px;
  display: flex;
  align-items:center;
  justify-content:space-around;
  div{
    font-family:Pixel;
    cursor:pointer;
    color:white;
    font-height:20px;
  }
`;
type Props = {
    setAboutModal: Dispatch<SetStateAction<boolean>>;
    addNavToRef:(el: HTMLDivElement) => void
};

function NavBar({setAboutModal,addNavToRef}: Props) {
  return (
    <Wrapper>
      <Item>
        <div ref={addNavToRef} id="main"></div>
        <div ref={addNavToRef} id="portfolio"></div>
        <div ref={addNavToRef} id="cemetery"></div>
        <div ref={addNavToRef} id="history"> </div>
        <div onClick={()=>setAboutModal(true)} id='personal'>  / </div>
      </Item>
    </Wrapper>
  );
}

export default NavBar;
